@use '../base/variables' as v;
@use '../base/mixins' as m;
.header {
    @include m.tablet {
        &.fijo {
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .1rem #333;
        }
    }
    background-color: v.$verde;
    h1 {
        color: v.$blanco;
        text-align: center;
        @include m.tablet {
            margin: 0;
        }
    }
    .contenido-header {
        padding: 2rem;
        @include m.tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
}

.navegacion-principal {
    @include m.tablet {
        display: flex;
    }
    a {
        padding: 0 2rem;
        color: v.$blanco;
        font-size: 2.4rem;
        display: block;
        text-align: center;
        margin-bottom: 1rem;
        @include m.tablet {
            margin: 0;
        }
        &:hover {
            color: v.$amarillo;
        }
    }
}