    @use '../base/variables' as v;
    @use '../base/mixins' as m;
    .video {
        position: relative;
        min-height: 45rem;
        max-height: 55rem;
        overflow: hidden;
        .overlay {
            position: absolute;
            height: 100%;
            width: 100%;
            background: rgb(150, 0, 255);
            background: linear-gradient(222deg, rgba(150, 0, 255, 0.5) 0%, rgba(255, 0, 110, 0.1) 100%);
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
        video {
            width: auto;
            display: block;
            @include m.desktop {
                width: 100%;
            }
        }
    }
    
    .contenido-video {
        z-index: 10; //como se muestra en la pagina, al frnte, o con numeros negativos al fondo
        h2 {
            color: v.$blanco;
            font-size: 4rem;
            margin: 0;
            @include m.telefono {
                font-size: 7rem;
            }
        }
        p {
            background-color: v.$rosa;
            display: inline-block;
            color: v.$blanco;
            padding: 1rem 2rem;
            font-weight: 700;
            font-size: 2rem;
        }
    }