    @use '../base/variables' as v;
    @use '../base/mixins' as m;
    .sobre-festival {
        display: block;
        padding: 5rem 0;
        @include m.tablet {
            @include m.grid(2, 5rem);
        }
    }
    
    .contenido-festival {
        display: flex;
        flex-direction: column;
        justify-content: center;
        h2 {
            margin: 2rem 0;
            @include m.tablet {
                margin: 0 0 2rem 0;
            }
        }
        .fecha {
            color: v.$rosa;
            font: 700;
        }
    }